import {FC} from "react";
import {Textarea} from "@/components/ui/textarea";
import {ChevronRight} from "lucide-react";
import {Controller, useForm} from "react-hook-form";
import {PromptFormType} from "@/components/Chat/PromptForm/type";
import {PROMPT_FORM_INITIAL_VALUE, PROMPT_FORM_VALIDATION_SCHEMA} from "@/components/Chat/PromptForm/config";
import {yupResolver} from "@hookform/resolvers/yup";
import {useChat} from "@/Provider/chatProvider";
import {ChatMessagesType, EnumChatMessageType} from "@/Provider/type";
import {getAnswer} from "@/api/textResponse";

type PromptFormProps = {
    isAnswerLoading: boolean;
    setIsAnswerLoading: (isAnswerLoading: boolean) => void;
}

const PromptForm: FC<PromptFormProps> = ({isAnswerLoading, setIsAnswerLoading}) => {
    const {control, reset, handleSubmit} = useForm<PromptFormType>({
        defaultValues: PROMPT_FORM_INITIAL_VALUE,
        resolver: yupResolver(PROMPT_FORM_VALIDATION_SCHEMA)
    })
    const {chatMessagesReducer} = useChat();


    const ScrollToBottom = () => {
        window.scrollTo({
            top: document.body.scrollHeight,
            behavior: 'smooth',
        });
    }

    const onSubmit = handleSubmit(async (values: PromptFormType) => {
        setIsAnswerLoading(true)

        const newMessages: ChatMessagesType = [];

        newMessages.push({
            type: EnumChatMessageType.USER,
            message: values.prompt,
        })

        try {
            const res = await getAnswer(values);

            newMessages.push({
                type: EnumChatMessageType.ROBOT,
                message: res,
            })
        } catch (error: unknown) {
            if (error instanceof Error) {
                console.log(error.message);
            } else {
                console.log(123)
            }
        }

        setIsAnswerLoading(false)


        chatMessagesReducer(newMessages)
        ScrollToBottom();
        reset();
    })

    return (
        <form onSubmit={onSubmit}
              className="flex max-w-[30%] w-full h-fit bg-transparent rounded-xl border-3 border-dark-border fixed bottom-16">
            <Controller
                render={({field}) => (
                    <Textarea
                        {...field}
                        name="prompt"
                        className="border-0 focus-visible:ring-0 resize-none shadow-none max-h-32"
                        placeholder="Ask whatever you want"
                    />
                )}
                name="prompt"
                control={control}
            />

            <button
                type="submit"
                className="p-2 rounded-[12px] w-fit h-full transition bg-dark-bg-secondary hover:bg-dark-text-secondary disabled:pointer-events-none disabled:bg-dark-text-secondary disabled:cursor-alias"
                disabled={isAnswerLoading}
            >
                <ChevronRight size={24} className="stroke-2"/>
            </button>
        </form>
    );
};

export default PromptForm;
